export default {
	data() {
		return {
			name:'赵鸿飞',
			data:[]
		}
	},
	created() {
		this.init()
	},
	mounted() {
		
	},
	methods: {
		//点击按钮判断状态
		zhipai:function(item){
			this.$store.state.Maintenancestatus = item.State  
			this.$store.state.MaintenancestatusID = item.MaintainId	
			uni.navigateTo({
				url:"./maintenance/maintenance"
			})
		},
		init(){
			//初始数据接口
				uni.showLoading({title: '加载中'});    
			 uni.request({
			        url: this.$store.state.url + 'Maintain/GetMaintains',
			        method: 'GET',
			        data: {
			            search : ''
			        },
			        header: {
			            'Token': this.$store.state.token
			        },
			        success: (res) => {
						uni.hideLoading();
			            if (JSON.parse(res.data).code == 0 || JSON.parse(res.data).Code == 0) {
							this.data = JSON.parse(res.data).Data
							for(var i = 0; i < this.data.length; i ++){
								this.data[i].BeginTime = this.data[i].BeginTime.split('T')[0]
							}
							console.log(this.data)
			            } else {
			                uni.showToast({
			                    title: "错误",
			                    icon: "none"
			                })
			            }
			        }
			    });
		}
	}
}
